/* eslint-disable */ //warning 무시하기
import "./App.css";
import { Navbar, Container, Nav, NavDropdown, Button, Form, FormControl } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import React, { useContext, useState } from "react";
import Data from "./data.js"; // Data 자리엔 자유롭게 작명가능
import { Link, Route, Switch, useHistory } from "react-router-dom";
import Detail from "./Detail.js";
import axios from "axios";



function App() {
  let [shoes, shoes변경] = useState(Data); //Data는 data.js 에 있는 데이터 전체
  let [재고, 재고변경] = useState([2, 3, 4]);
  let [더보기, 더보기변경] = useState(true)

  return (
    <div className="App">
      <Navbar bg="light" expand="lg" variant="light">
    <Container fluid> 
    {/* Form, FormControl 쓰려면 Container 에 fluid 속성 필요! */}
    <Navbar.Brand href="/">Hochony Shop</Navbar.Brand>
    <Nav className="me-auto">
      <Nav.Link as={Link} to="/">Home</Nav.Link>
      <Nav.Link as={Link} to="/detail">Top10</Nav.Link>
      <Nav.Link as={Link} to="/">Cart</Nav.Link>
    </Nav>
    <Form className="d-flex"> 
        <FormControl
          type="search"
          placeholder="Search"
          className="me-2"
          aria-label="Search"
        />
        <Button variant="outline-success">Search</Button>
      </Form>
    </Container>
  </Navbar>
      {/* Switch 쓰면 하나하나 exact 안 붙여도 됨!  */}
      <Switch>
        <Route exact path="/">
          <div className="Jumbotron">
            <h1>20% Season Off</h1>
            <h4>싱싱한 새벽배송</h4>
            <Button variant="light">자, 이제 당신도 호집사</Button>
          </div>
          <div className="container">
            
              <div className="row">
                {shoes.map((a, i) => {
                  return <Card shoes={shoes[i]} i={i} key={i} 재고={재고} 재고변경={재고변경}/>; //shoes 중에 shoes[i] 만 전송한다
                })}
              </div>
            

            { 더보기 === true ?
            <button
              className="btn btn-warning"
              onClick={() => {
                axios //axios는 JSON 을 예쁘게 Object 형으로 바꿔줌 즉 따옴표 다 떼줌! fetch는 그런거 없음ㅅㄱ
                  .get("https://ziuss76.github.io/data2.json") //get 요청 할 주소
                  .then((result) => {
                    //then 은 요청 성공시 실행할 코드, result.data 는 받아온 데이터
                    shoes변경([...shoes, ...result.data]); //data 카피본에 추가적으로 data2 카피본 넣기
                    더보기변경(false);
                  })
                  .catch(() => {
                    //catch 는 요청 실패시 실행할 코드
                    console.log("불러오기 실패!");
                  });
              }}
            >
              더보기
            </button> : null}
          </div>
        </Route>
        <Route path="/detail/:id">
          <Detail shoes={shoes} 재고={재고} 재고변경={재고변경} />
        </Route>
        <Route path="/cart">
        </Route>
      </Switch>
    </div>
  );
}

function Card(props) {
  let history = useHistory();
  
  return (
    <div className="col-md-4">
      <img
        onClick={() => {
          history.push("/detail/" + props.i);
        }}
        src={"images/hochony" + (props.i + 115) + ".jpg"
        
        }
        alt="" width="100%"
      />
      <h4 className="photoTitle">{props.shoes.title}</h4>
      <p>
        {props.shoes.content}
      </p>
      <p>{props.shoes.price}원 / 재고: {props.재고[0]}</p>
    </div>
  );
}

export default App;
